
:root {
  --primary-light-blue: #83BCFF;
  --secondary-dark-grey: #454749;
  --accent-red: #FF2020;
  --Font-White: #F5F5F5;
  --Font-Black: #111111;
  --background: #333333;
  --link: #9747FF;
}

.Navbar li {
 list-style:  none;
 text-decoration: none;

 padding-left: 5%;
 padding-right: 5%;
}

.Navbar {
  display: flex;
  align-items: center;
  padding: 0;
}

@media (max-width: 600px) {
  .Navbar {
    flex-direction: column;
    align-items: flex-start;
  }

  .Navbar li {
    padding-bottom: min(5%, 10px);
  }

  .Robco-Logo {
    margin-right: 0;
    margin-bottom: 1%;
    width: 80%;
    align-self: center;
  }
}

a {
  text-decoration:  none;
  color: var(--link);
}

